article.creators {
  width: 90vw;
  padding-left: 5vw;
  padding-right: 5vw;
  background-color: #f9f9f9;
}
article.creators h2 {
  color: #141419;
}
article.creators div.creators-list {
  display: flex;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
  margin-bottom: 200px;
  margin-top: 100px;
}
article.creators div.creators-list a.creator {
  border-radius: 25px;
  overflow: hidden;
  width: 350px;
  height: 475px;
  box-shadow: 30px 15px 80px 0px rgba(191, 191, 191, 0.6);
  background: #f2f2f2;
  transition: all 0.5s 0.1s ease;
  position: relative;
  top: 0;
}
article.creators div.creators-list a.creator:hover {
  box-shadow: 30px 15px 80px 0px #bfbfbf;
  top: -10px;
  background: #f9f9f9;
}
article.creators div.creators-list a.creator img[alt='bg'] {
  width: 100%;
  height: 150px;
}
article.creators div.creators-list a.creator div.info {
  position: relative;
  top: -60px;
  padding: 20px;
}
article.creators div.creators-list a.creator div.info .profile-photo {
  display: flex;
  justify-content: center;
}
article.creators div.creators-list a.creator div.info .profile-photo img {
  border-radius: 50%;
  border: 6px solid #fff;
  width: 100px;
  background-color: #fff;
}
article.creators div.creators-list a.creator div.info h3 {
  color: black;
  font-family: Poppins;
  font-size: 23px;
  text-align: center;
  font-weight: 600;
  line-height: 0px;
}
article.creators div.creators-list a.creator div.info .info-hr {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
article.creators div.creators-list a.creator div.info .info-hr .col {
  margin-bottom: 20px;
}
article.creators div.creators-list a.creator div.info .info-hr .col .var {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  height: 25px;
}
article.creators div.creators-list a.creator div.info .info-hr .col .var h5 {
  color: #141416;
  text-align: center;
  font-family: Poppins;
  font-size: 17px;
  font-weight: 600;
  line-height: 10px;
  margin: 0;
}
article.creators div.creators-list a.creator div.info .info-hr .col p {
  color: #828282;
  text-align: center;
  font-family: Poppins;
  font-size: 13px;
  font-weight: 400;
  line-height: 12px;
}
article.creators div.creators-list a.creator div.info .works {
  display: flex;
  justify-content: center;
  gap: 5px;
  align-items: center;
}
