header .open-nav {
  display: none;
}
header nav {
  padding: 20px 3vw;
  background-color: #F9F9F9;
  display: flex;
  gap: 5vw;
  align-items: center;
  justify-content: center;
}
header nav img[alt="logo"] {
  margin-right: 5vw;
}
header nav .nav-menu {
  display: flex;
  gap: 3vw;
}
header nav .nav-menu a.nav-el {
  color: #606060;
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
}
header nav .nav-menu a.nav-el:hover {
  color: black;
}
header nav .nav-menu a.nav-el.active {
  font-weight: 600;
  color: #141416;
}
header nav input {
  width: 20vw;
  height: 50px;
  background-color: #EDEDED;
  border-radius: 16px;
  outline: none;
  padding: 0 1vw;
  font-size: 16px;
  font-family: Poppins;
  border: none;
}
header nav input::placeholder {
  font-family: Poppins;
  font-size: 14px;
}
header nav button.sign {
  background-color: #141416;
  border-radius: 16px;
  font-size: 16px;
  color: white;
  height: 50px;
  padding: 0px 5vw;
  border: none;
  cursor: pointer;
}
header nav .login {
  height: 50px;
  border-radius: 16px;
  display: flex;
  align-items: center;
}
header nav .login:hover {
  background-color: #edecec;
}
header nav .login img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  width: fit-content;
}
header nav .login p {
  font-weight: 700;
  font-family: Poppins;
  color: #141416;
  text-decoration: none;
}
@media screen and (max-width: 1170px) {
  header#header-nav nav {
    padding: 20px 10px;
    gap: 10px;
    width: calc(100vw - 20px);
    justify-content: space-between;
  }
  header#header-nav nav img[alt='logo'] {
    margin-right: 1vw;
  }
  header#header-nav nav .login {
    height: fit-content;
  }
  header#header-nav nav .login p {
    display: none;
    word-wrap: break-word;
  }
  header#header-nav nav .login img {
    position: relative;
    left: -100%;
  }
}
@media screen and (max-width: 770px) {
  header#header-nav nav {
    gap: 10px;
    justify-content: center;
  }
}
@media screen and (max-width: 560px) {
  header#header-nav nav {
    z-index: 4;
    gap: 10px;
    justify-content: space-between;
  }
  header#header-nav .nav-menu {
    display: none;
  }
  header#header-nav input {
    display: none;
  }
  header#header-nav .login {
    display: none;
  }
  header#header-nav .sign {
    display: none;
  }
  header#header-nav .open-nav {
    display: block;
    border: none;
    padding: 10px;
  }
  header#header-nav .open-nav input#open {
    display: block;
    visibility: hidden;
  }
  header#header-nav .open-nav label {
    display: flex;
    flex-direction: column;
    gap: 5px;
    position: relative;
    left: 20px;
    bottom: 33px;
  }
  header#header-nav .open-nav label .btn-line {
    width: 30px;
    height: 1px;
    background-color: black;
    transition: all 0.5s ease;
    position: relative;
  }
  header#header-nav .open-nav input#open:checked + label .line1 {
    bottom: -3px;
    transform: rotate(0.125turn);
  }
  header#header-nav .open-nav input#open:checked + label .line2 {
    display: none;
  }
  header#header-nav .open-nav input#open:checked + label .line3 {
    top: -3px;
    transform: rotate(-0.125turn);
  }
  header#header-nav .back {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 2;
  }
  header#header-nav .menu-hidden {
    display: block;
    position: absolute;
    z-index: 3;
    top: 100px;
    background-color: #F9F9F9;
    width: 100vw;
    margin-right: 0;
    animation: animAnim 0.5s ease;
  }
  header#header-nav .menu-hidden input {
    display: block;
    width: 80vw;
    margin-top: 10px;
  }
  header#header-nav .menu-hidden .login#login {
    z-index: 3;
    display: flex;
    width: 80vw;
    margin-top: 10px;
  }
  header#header-nav .menu-hidden .login#login p {
    z-index: 3;
    position: relative;
    display: block;
  }
  header#header-nav .menu-hidden .login#login img {
    z-index: 3;
    position: relative;
    display: block;
  }
  header#header-nav .menu-hidden .sign {
    display: block;
    width: 50vw;
    margin-top: 10px;
  }
  header#header-nav .menu-hidden .nav-menu-hidden {
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: #141414;
  }
  header#header-nav .menu-hidden .nav-menu-hidden a {
    color: #606060;
    transition: all 0.3s ease;
  }
  header#header-nav .menu-hidden .nav-menu-hidden a.active {
    color: #141414;
    font-weight: 600;
  }
}
@keyframes animAnim {
  from {
    top: -200px;
  }
  to {
    top: 100px;
  }
}
