footer {
  background: #141416;
}
footer div.lvl1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 10vw;
  flex-wrap: wrap;
}
footer div.lvl1 div.logo-container {
  display: flex;
  align-items: center;
  gap: 10px;
}
footer div.lvl1 div.logo-container img {
  height: 60px;
}
footer div.lvl1 div.logo-container p.logo {
  color: white;
  font-size: 30px;
  font-style: normal;
  font-weight: 600;
}
footer div.lvl1 nav {
  display: flex;
  gap: 5vw;
}
footer div.lvl1 nav a {
  color: #B9B9B9;
  font-family: Inter;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.3s 0.1s ease;
}
footer div.lvl1 nav a:hover {
  color: white;
}
footer .line {
  width: 80vw;
  height: 1px;
  opacity: 0.25;
  background: #818181;
  margin-left: 10vw;
}
footer div.lvl2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 10vw;
  flex-wrap: wrap;
}
footer div.lvl2 p {
  color: #999;
  font-weight: 500;
  font-size: 17px;
}
