.head {
  min-height: 100vh;
  max-height: fit-content;
  display: flex;
  justify-content: space-between;
  background-color: #F9F9F9;
  padding-top: 64px;
}
.head .left {
  padding: 0 5vw;
  width: 40%;
}
.head .left .flex-line .line {
  width: 200px;
  height: 1px;
  background-color: #EDEDED;
}
.head .left .flex-line p {
  display: none;
  color: #EDEDED;
  font-family: Poppins;
  font-size: 9.248px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  /* 11.097px */
  letter-spacing: 1.233px;
}
.head .left h1 {
  font-family: Poppins;
  font-size: 75px;
  line-height: 90px;
  margin: 0;
}
.head .left .descript {
  width: 30vw;
  color: #676767;
  position: relative;
  padding: 20px 0;
}
.head .left .descript .bold {
  font-weight: 700;
  color: black;
}
.head .left .main-btns {
  display: flex;
  gap: 2vw;
}
.head .left .main-btns .btn {
  cursor: pointer;
  border-radius: 12px;
  padding: 18px 30px;
  font-size: 16px;
  position: relative;
  transition: top 0.6s ease;
  top: 0;
}
.head .left .main-btns .btn:hover {
  top: -3px;
}
.head .left .main-btns .explore {
  color: white;
  background-color: black;
  border: none;
}
.head .left .main-btns .create {
  color: black;
  background-color: white;
  border: 1px solid black;
}
.head .left .footer-head {
  padding-top: 50px;
  display: flex;
  gap: 4vw;
}
.head .left .footer-head h3 {
  font-family: Sans;
  font-size: 36px;
  margin: 0;
}
.head .left .footer-head p {
  color: #848586;
}
.head .right {
  position: absolute;
  right: 0;
  display: flex;
  gap: 2vw;
}
.head .right .col-2 {
  display: grid;
  gap: 20px ;
}
.head .right .col-2 .top {
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 1fr;
}
.head .right .col-2 .top .spin-arrow {
  position: relative;
  top: 50px;
}
@media screen and (max-width: 1270px) {
  .head .left .flex-line .line {
    width: 150px;
  }
  .head .left .flex-line p {
    font-size: 8px;
    line-height: 100%;
  }
  .head .left h1 {
    font-size: 60px;
    line-height: 75px;
  }
  .head .left .descript {
    padding: 20px 0;
  }
  .head .left .main-btns {
    gap: 1vw;
  }
  .head .left .main-btns .btn {
    padding: 12px 20px;
    font-size: 13px;
  }
  .head .left .footer-head {
    gap: 2vw;
  }
  .head .left .footer-head h3 {
    font-size: 25px;
  }
  .head .right {
    justify-content: end;
    gap: 10px;
  }
  .head .right img {
    width: 80%;
  }
  .head .right .col-1 {
    width: 316px;
    height: 253px;
    justify-content: end;
    display: flex;
    position: relative;
    right: -30px;
  }
  .head .right .col-2 {
    gap: 2vw ;
    justify-content: end;
  }
  .head .right .col-2 .top {
    gap: 10px;
    justify-content: end;
  }
  .head .right .col-2 img {
    position: relative;
    right: -30px;
  }
}
@media screen and (max-width: 840px) {
  .head {
    padding-top: 30px;
    display: flex;
    justify-content: center;
    text-align: center;
    height: fit-content;
  }
  .head .right {
    display: none;
  }
  .head .left {
    padding: 0 5vw;
    width: 90%;
  }
  .head .left .flex-line {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }
  .head .left .flex-line .line {
    height: 2px;
    width: 50px;
    background-color: #d8d8d8;
  }
  .head .left .flex-line p {
    display: block;
    color: #d8d8d8;
  }
  .head .left .descript {
    width: 90vw;
  }
  .head .left .main-btns {
    justify-content: center;
  }
  .head .left .footer-head {
    justify-content: center;
  }
}
