article.stats {
  background: #F9F9F9;
  padding-bottom: 150px;
}
article.stats h2.Top {
  font-weight: 600;
  padding: 100px 0 50px 0;
  color: #141416;
}
article.stats table {
  width: 80vw;
  margin: 10px 10vw;
  border-collapse: collapse;
}
article.stats table tr {
  border-bottom: 2px solid #EBE9E9;
  transition: all 0.5s 0.1s ease;
}
article.stats table tr:nth-child(1) {
  border: none;
}
article.stats table tr th {
  color: #1A202C;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 20px auto;
}
article.stats table tr th:not(:nth-child(1)):hover {
  background-color: #eeeeee;
}
article.stats table tr th:nth-child(1) {
  cursor: default;
  color: #93989A;
}
article.stats table tr td div {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  flex-wrap: wrap;
}
article.stats table tr td.name div {
  justify-content: start;
  gap: 20px;
}
article.stats table tr td.name div img {
  border-radius: 50%;
  width: 60px;
  height: 60px;
}
article.stats table tr td.name div div.name-info {
  display: block;
}
article.stats table tr td.name div div.name-info .name {
  font-weight: 600;
  font-size: 22px;
  color: #141416;
  line-height: 100%;
}
article.stats table tr td.name div div.name-info .by {
  font-size: 17px;
  font-weight: 400;
  color: #64748B;
  line-height: 20%;
}
article.stats table tr td.volume {
  color: black;
  font-weight: 700;
  padding: 0;
}
article.stats table tr td.volume div {
  padding: 0;
}
article.stats table tr td.volume p.hide-elem {
  display: none;
  color: #10C352;
  font-weight: 600;
  font-size: 15px;
  margin: 0;
}
article.stats table tr td.procent {
  color: #10C352;
  font-weight: 600;
}
article.stats table tr td.price {
  color: black;
  font-weight: 700;
}
article.stats table tr td.owners {
  color: black;
  font-weight: 600;
}
article.stats table tr td.items {
  color: black;
  font-weight: 600;
}
@media screen and (max-width: 900px) {
  article.stats table {
    width: 100vw;
    margin: 0;
    border-collapse: collapse;
  }
}
