
div.modal {
  padding: 2vw 5vw;
  height: fit-content;
  background-color: #F9F9F9 ;
  border-radius: 8px;
  width: fit-content;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 10000;
  transform: translate(-50%,-50%);
  animation: placeBidAnim 0.5s ease ;
}
div.modal h1 {
  font-weight: 600;
  font-size: 16px;
}
div.modal form {
  width: fit-content;
  margin: 0;
}
.error {
  color: red
}
div.modal form input {
  width: 90%;
  height: 50px;
  background-color: #EDEDED;
  border-radius: 16px;
  outline: none;
  margin-bottom: 10px ;
  padding: 0 1vw;
  font-size: 16px;
  font-family: Poppins;
  border: none;
}
div.modal form input::placeholder {
  font-family: Poppins;
  font-size: 14px;
}
div.modal form button {
  background-color: #141416;
  border-radius: 16px;
  font-size: 16px;
  color: white;
  border: none;
  cursor: pointer;
  width: 40%;
  height: 50px;
  margin-top: 20px;
  padding: 0 1vw;
}
div.modal form button:hover {
  background-color: #111111;
}
