div.card {
  width: 86vw;
  padding-left: 7vw;
  padding-right: 7vw;
  background: #F9F9F9;
}
div.card button.back {
  background: none;
  padding: 10px 10px 10px 0;
  display: flex;
  gap: 30px;
  align-items: center;
  color: #23262F;
  font-family: Poppins;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 50px;
  border: none;
  cursor: pointer;
  transition: left 0.5s 0.1s ease;
  position: relative;
  left: 0;
}
div.card button.back:hover {
  left: -10px;
}
div.card div.place {
  border-radius: 24.423px;
  background: #FAFAFA;
  box-shadow: 17.5px 17.5px 61px 0px rgba(0, 0, 0, 0.12);
  width: calc(86vw - 36px * 2);
  display: flex;
  justify-content: space-around;
  padding: 1.8vw;
  margin: 0 0 100px 0;
  flex-wrap: wrap;
}
div.card div.place img {
  width: 500px;
  height: 500px;
  border-radius: 23px;
}
div.card div.place div.info {
  width: 470px;
}
div.card div.place div.info h2 {
  color: #292B39;
  font-family: Poppins;
  font-size: 50px;
  font-weight: 600;
  line-height: 32px;
}
div.card div.place div.info p.descript {
  color: rgba(136, 136, 136, 0.7);
  font-family: Poppins;
  font-size: 19px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
}
div.card div.place div.info div.persons {
  display: flex;
  justify-content: space-between;
}
div.card div.place div.info div.persons div.person {
  display: flex;
  align-items: center;
  gap: 15px;
}
div.card div.place div.info div.persons div.person img {
  width: 65px;
  height: 65px;
}
div.card div.place div.info div.persons div.person div.info-person {
  position: relative;
  bottom: -10px;
}
div.card div.place div.info div.persons div.person div.info-person .gh {
  color: #8D8D8D;
  font-family: Poppins;
  font-size: 17px;
  font-weight: 400;
  line-height: 100%;
  margin: 0;
}
div.card div.place div.info div.persons div.person div.info-person h4.name-person {
  color: black;
  font-family: Poppins;
  font-size: 25.5px;
  font-weight: 500;
  line-height: 0px;
  position: relative;
  margin-top: 20px;
}
div.card div.place div.info .info-bottom {
  justify-content: space-between;
  display: flex;
}
div.card div.place div.info .info-bottom div.current p {
  color: #8D8D8D;
  font-family: Poppins;
  font-size: 17px;
  font-weight: 400;
}
div.card div.place div.info .info-bottom div.current strong {
  display: flex;
  gap: 5px;
  align-items: center;
  color: #292B39;
  font-family: Poppins;
  font-size: 33px;
  font-weight: 600;
}
div.card div.place div.info .info-bottom div.current strong img {
  height: 30px;
  width: 25px;
}
div.card div.place div.info .info-bottom div.date p {
  color: #8D8D8D;
  font-family: Poppins;
  font-size: 17px;
  font-weight: 400;
  text-align: right;
}
div.card div.place div.info .info-bottom div.date strong {
  display: flex;
  gap: 5px;
  align-items: center;
  color: black;
  text-align: right;
  font-family: Poppins;
  font-size: 19px;
  font-weight: 400;
  line-height: 30px;
}
div.card div.place div.info button {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  align-items: center;
  gap: 16px;
  border-radius: 19px;
  background: #141416;
  width: 100%;
  color: white;
  font-family: Poppins;
  font-size: 18px;
  font-weight: 700;
  line-height: 28px;
  padding: 20px 0;
  cursor: pointer;
  border: none;
  position: relative;
  top: 0;
  transition: all 0.5s 0.1s ease;
}
div.card div.place div.info button:hover {
  top: -5px;
  -webkit-box-shadow: 0px 18px 16px 1px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 0px 18px 16px 1px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 18px 16px 1px rgba(34, 60, 80, 0.2);
}
div.card div.place div.info button img {
  border-radius: 0;
  width: 22px;
  height: 22px;
}
@media screen and (max-width: 666px) {
  .card#card {
    padding-left: 0;
    padding-right: 0;
    width: 98vw;
  }
  .card#card .place {
    width: 98vw;
  }
  .card#card .place img[alt='NFT'] {
    width: 96vw;
    height: 96vw;
  }
  .card#card .place .persons {
    flex-wrap: wrap;
  }
}
